.page-footer {
  background-color: $black;
  color: $white;
  flex-shrink: 0;
}

.page-footer__wrapper {
  display: flex;
  justify-content: space-between;
  max-width: 1140px;
  margin-left: auto;
  margin-right: auto;
  padding-bottom: 100px;

  @media (max-width: $tablet) {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-template-rows: auto auto;
    margin-left: 45px;
    padding-bottom: 32px;
  }

  @media (max-width: $mobile) {
    margin-left: 0;
    margin-right: 0;
    display: flex;
    flex-direction: column;
    padding-bottom: 40px;
  }
}

.page-footer__links {
  display: flex;
  flex-direction: column;
  margin-top: 94px;

  @media (max-width: $tablet) {
    margin-top: 45px;
    margin-left: 15px;
  }

  @media (max-width: $mobile) {
    margin-top: 47px;
    margin-left: 15px;
  }
}

.page-footer__links h2 {
  display: none;
}

.page-footer__links a {
  color: $white;
  line-height: 21px;
  margin-bottom: 21px;
  text-decoration: none;

  &:focus,
  &:hover {
    color: $brown;
  }

  &:active {
    opacity: 0.7;
  }

  @media (max-width: $mobile) {
    margin-bottom: 20px;
  }
}

.page-footer__links a > svg {
  margin-right: 15px;
  fill: $white;

  @media (max-width: $mobile) {
    height: 21px;
    width: 20px;
    margin-right: 30px;
  }
}

.page-footer__links a:focus > svg,
.page-footer__links a:hover > svg {
  fill: $brown;
}

.page-footer__links:nth-child(2n) {
  margin-left: 40px;

  @media (max-width: 1260px) {
    margin-left: 0;
  }

  @media (max-width: $tablet) {
    margin-left: 0;
  }

  @media (max-width: $mobile) {
    margin-left: 15px;
    margin-bottom: -25px;
  }
}

.page-footer__links:last-child {
  margin-right: -15px;

  @media (max-width: 1250px) {
    margin-right: 5px;
  }

  @media (max-width: $tablet) {
    margin-right: 0;
    margin-left: -25px;
  }

  @media (max-width: $mobile) {
    display: flex;
    flex-direction: row;
    margin-left: 10px;
    margin-top: 30px;
  }
}

.page-footer__links:last-child a > span {
  @media (max-width: $mobile) {
    display: none;
  }
}
